import React from "react";
import ReactDOM from "react-dom/client";
import Tres from "./TresEnRaya.js";
import Aho from "./Ahorcado.js";
import  "./index.css";


let ClasesTres = "JuegoTres";
let ClasesAho = "JuegoAho oculto";
let ClasesBottonTres = "BotonTres botonSeleccionado";
let ClasesBottonAho = "BotonAhorcado";

function handleClickTres()
{
    ClasesTres = "JuegoTres";
    ClasesAho = "JuegoAho oculto";
    ClasesBottonTres = "BotonTres botonSeleccionado";
    ClasesBottonAho = "BotonAhorcado";
    root.render(<Juegos  />);
}

function handleClickAho()
{
    ClasesTres = "JuegoTres oculto";
    ClasesAho = "JuegoAho";
    ClasesBottonTres = "BotonTres";
    ClasesBottonAho = "BotonAhorcado botonSeleccionado";
    root.render(<Juegos  />);
}

const TresEnRaya = () => <div className="TresEnRaya"><Tres /></div>
const Ahorcado = () => <div className="Ahorcado"><Aho /></div>
const Juegos = () =>
    <div className="Juegos">
        <div className = "SelectorJuegos">
            <div className = "NombreJuegos"><button className={ClasesBottonTres} onClick={handleClickTres}>Tres en raya</button></div>    
            <div className = "NombreJuegos"><button className={ClasesBottonAho} onClick={handleClickAho}>Ahorcado</button></div>    
        </div>
        <div className = "ContenedorJuegos">
            <div className={ClasesTres}>
                <TresEnRaya />
            </div>
            <div className={ClasesAho}>
            <Ahorcado />
            </div>
        </div>
    </div>
//export default Juegos;
// ========================================

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(<Juegos  />);
