import { render } from "@testing-library/react";
import { getValue, setSelectionRange } from "@testing-library/user-event/dist/utils";
import React from "react";
//import ReactDOM from "react-dom/client";
import "./ahorcado.css";

import imagen0 from "./images/aho-0.png" ;
import imagen1 from "./images/aho-1.png" ;
import imagen2 from "./images/aho-2.png" ;
import imagen3 from "./images/aho-3.png" ;
import imagen4 from "./images/aho-4.png" ;
import imagen5 from "./images/aho-5.png" ;
import imagen6 from "./images/aho-6.png" ;
import imagen7 from "./images/aho-7.png" ;
import imagen8 from "./images/aho-8.png" ;
import imagen9 from "./images/aho-9.png" ;
import imagen10 from "./images/aho-10.png" ;


const imagenesAhorcado = 
[
  imagen0,imagen1, imagen2, imagen3, imagen4, imagen5, imagen6, imagen7, imagen8, imagen9, imagen10
];
const palabras = 
[
  "BOTELLA","CERVEZA","CALABAZA", "MANZANA", "RELOJ"
];

function Linea(props) {
  return (
    <span className="linea">
      <span className="letra">{props.value}</span>
    </span>
  );
}


class Board extends React.Component {
  
  render() {
    return (
      <div>
        <div className="palabra">
        {
          this.props.lineasPrintar
        }
        </div>
      </div>
    );
  }
}

class Aho extends React.Component {

  constructor(props) {
    super(props);
    this.state = 
    {
      boolJuegoIniciado: false,
      boolGanador: false,
      boolJuegoTerminado: false,
      inputSeleccionado: '',
      totalIntentos: 10,
      intentosFallidos: 0,
      intentosBuenos: 0,
      boolPalabraSeleccionada: false,
      lineasPalabra: [],
      letrasPalabra: [],
      letrasProbadas: [],
      cadenaLetras: ""
    }

    this.handleClickInicio = this.handleClickInicio.bind(this);
    this.handleClickLetra = this.handleClickLetra.bind(this);
    this.handleClickRestart = this.handleClickRestart.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleClickInicio()
  {
    this.setState({
      boolJuegoIniciado: true,
      boolGanador: false,
      boolJuegoTerminado: false,
      boolPalabraSeleccionada: false,
      intentosBuenos: 0,
      intentosFallidos: 0,
      cadenaLetras: "",
      letrasProbadas: []
    });

      let seleccionado = this.state.boolPalabraSeleccionada;
      const lineas = [];
      const letras = [];
      const numPalabras = palabras.length;

        let random = parseInt(Math.random() * numPalabras);
        for(let i = 0; i < palabras[random].length; i++)
        {
          lineas[i] = this.renderLinea(i);
          letras[i] = palabras[random][i];
        }
        this.setState({
          boolPalabraSeleccionada: true,
          lineasPalabra: lineas,
          letrasPalabra: letras
        });
  }

  handleChange(event)
  {
    this.setState({
      inputSeleccionado: event.target.value
    })
  }

  handleClickLetra()
  {
    const letraSeleccionada = this.state.inputSeleccionado;
    const lestrasEnPalabra = this.state.letrasPalabra;
    const letraProbadas = this.state.letrasProbadas;
    let fallos = this.state.intentosFallidos;
    let letraEncontrada = false;

    if(letraSeleccionada == '' || letraSeleccionada == ' ') return false;

    for(let i = 0; i < lestrasEnPalabra.length; i++)
    {
      if(lestrasEnPalabra[i] == letraSeleccionada.toUpperCase())
      {
        this.setLetra(i, letraSeleccionada.toUpperCase());
        letraEncontrada = true;
      }
    }

   


    if(!letraEncontrada)
    {
      fallos ++;
      if(fallos < this.state.totalIntentos) 
      this.setState({
        intentosFallidos: fallos,
        inputSeleccionado: ""
      })
      else
      {
        for(let i = 0; i < lestrasEnPalabra.length; i++)
        {
            this.setLetra(i, lestrasEnPalabra[i]);
        }
        this.setState({
          boolJuegoTerminado: true,
          boolGanador: false
        })
      }
    }

    if(!letraProbadas.includes(letraSeleccionada)) letraProbadas.push(letraSeleccionada);

    this.renderLetras()
    this.setState({
      letrasProbadas: letraProbadas
    })
  }


  handleClickRestart()
  {
    this.handleClickInicio();
  }


  renderLinea(i) {
    return (
      <Linea value = {" "} key = {i}/>
    );
  }

  setLetra(posicion, letra)
  {
    const newLineas = this.state.lineasPalabra;
    let letrasFaltantes = 0;
    let ganador = false;
    newLineas[posicion]= <Linea value = {letra} key = {newLineas[posicion].key}/>;

    for(let i = 0; i < newLineas.length; i++)
    {
      if(newLineas[i].props.value == ' ') letrasFaltantes ++
    }

    if(letrasFaltantes == 0) ganador = true;
    this.setState({
      inputSeleccionado: "",
      lineasPalabra: newLineas,
      boolJuegoTerminado: ganador,
      boolGanador: ganador
    })
  }

  renderLetras()
  {
    let listado = "";
    for(let i = 0; i < this.state.letrasProbadas.length; i ++)
    {
      listado = listado  + this.state.letrasProbadas[i]+ " ";
    }
    this.setState({
       cadenaLetras: listado
    })
  }

  calcularWinLose()
  {

  }

  render() {

    const juegoIniciado = this.state.boolJuegoIniciado;
    const juegoTerminado = this.state.boolJuegoTerminado;
    

    if(juegoIniciado)
    {
      if(!juegoTerminado)
      {
        
        return (
          <div className="game-aho">
            <div className="game-intentosRestantes">
               <img src={imagenesAhorcado[this.state.intentosFallidos]}/>
            </div>
            <div className="game-board-aho">
              <Board 
              lineasPrintar = {this.state.lineasPalabra} />
              <div>
                <input type="text" maxLength = "1" className = "InputLetra" value={this.state.inputSeleccionado}
                onChange={this.handleChange} autoFocus></input>
                <button className="botonLetra" onClick={this.handleClickLetra}>Probar</button>
              </div>
            </div>
            <div className="game-info">
              
              <div> Letras probadas: {this.state.cadenaLetras/*Listado de letras*/}</div>
            </div>
          </div>
        );
        
      }
      else
      {
        let ganadorPerdedor = "Perdiste";
        let estadoAhorcado = 10;
        let clasesDivGanador = "game-win perdedor";
        if(this.state.boolGanador)
        {
            ganadorPerdedor = "Ganaste";
            estadoAhorcado = this.state.intentosFallidos;
            clasesDivGanador = "game-win ganador";
        } 

        return (
          <div className="game-aho">
             <div className="game-intentosRestantes">
               <img src={imagenesAhorcado[estadoAhorcado]}/>
            </div>
            <div className="game-board-aho">
              <Board 
              lineasPrintar = {this.state.lineasPalabra} />
              <div>
                <div className={clasesDivGanador}>{ganadorPerdedor}</div>
                <button className="botonRestart" onClick={this.handleClickRestart}>Reiniciar</button>
              </div>
            </div>
          </div>
        )
      }
    }
    else
    {
      return (
      <div className="game">
        <div className="game-board-aho">
          <button className="botonInicio" onClick={this.handleClickInicio}>Jugar</button>
        </div>
        <div className="game-info">
          <div>{/**/ }</div>
          <ol>{/**/}</ol>
        </div>
      </div>
    );
  }

    

    


    
  }
}



export default Aho;
